* {
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  color: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

.container {
  background: url('../images/bg--xl.jpg');
  background-position: center;
  background-size: contain;
  height: 100vh;
  width: 100vw;
  overflow: hidden;

  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
}

.btn {
  background: transparent;
  border: none;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  cursor: pointer;
  font-weight: 700;
  font-size: 20px;
  color: white;
  text-transform: uppercase;
}

.btn--bet {
  background-image: url("data:image/svg+xml,%3Csvg width='296' height='83' viewBox='0 0 296 83' fill='none' xmlns='http://www.w3.org/2000/svg' %3E%3Cpath d='M2 32.7973L18.2801 2H294.5V48.5541L277.135 81.5H2V32.7973Z' stroke='%2302A5DF' stroke-width='3' /%3E bet %3C/svg%3E");

  width: 300px;
  height: 80px;
}

.btn--challenge {
  height: 240px;
  width: 240px;
  border: 3px solid #ff21d0;
  margin: 20px 20px 40px;
  font-size: 5em;
}

.main {
  height: 100%;
  width: 100%;
  position: relative;
}

#section-form {
  position: absolute;
  z-index: 3;
  display: none;
  align-items: center;
  justify-content: center;
  background-color: #001139;
  position: absolute;
  height: calc(100vh - 120px);
  width: 100vw;
  top: 0;
  left: 0;
}

.form {
  display: flex;
  flex-direction: column;

  &--input {
    display: flex;
  }

  &--group {
    display: flex;
    flex-direction: column;
    margin: 20px;
  }
}

.input {
  height: 400px;
  padding: 20px;
  overflow: auto;
  background-color: transparent;
  border: none;

  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;

  font-size: 5em;
  color: white;
  text-align: center;

  &:focus {
    outline: none;
  }
}

.label {
  color: white;
  font-size: 2em;
  text-transform: uppercase;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  display: none;
  -webkit-appearance: none;
  margin: 0;
}

input[type='number'] {
  -moz-appearance: textfield;
}

input[name='bet'] {
  width: 500px;
  // background-image: url("data:image/svg+xml,%3Csvg width='495' height='250' viewBox='0 0 495 250' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2 32.7348L29.3284 2H493V215.121L463.85 248H2V32.7348Z' fill='%23001139' stroke='%2302A5DF' stroke-width='3'/%3E%3C/svg%3E%0A");
  border: 3px solid #02a5df;
}

input[name='secret'] {
  width: 250px;
  // background-image: url("data:image/svg+xml,%3Csvg width='304' height='254' viewBox='0 0 304 254' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2 33.2346L18.6976 2H302V218.586L284.189 252H2V33.2346Z' fill='%23001139' stroke='%23FF21D0' stroke-width='3'/%3E%3C/svg%3E%0A");
  border: 3px solid #ff21d0;
}

input[type='submit'] {
  font-size: 2em;
}

#section-challenge {
  position: absolute;
  z-index: 2;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  color: white;
  text-transform: uppercase;

  background-color: #001139;
  position: absolute;
  height: calc(100vh - 120px);
  width: 100vw;
  top: 0;
  left: 0;
}
